import React, { useState } from 'react';
import './css/crafting_categories.css'
import 'bulma/css/bulma.min.css';

import { usePopper } from 'react-popper';

var crafting_categories = {1: "Food", 2: "Electronics"}

function get_amount_in_inventory(item_id, inventory) {

	let items = inventory
					.filter((item) => item.item_id == item_id)
					.map((item) => item.amount)

	if (items.length == 0) {

		return 0

	}

	return items.reduce((accumulator, current_value) => accumulator + current_value)

}

function max_crafting_amount(recipe, inventory) {

	let recipe_inputs = recipe.inputs.map((input) => {
		return {itemid: input.itemid, amount: input.amount}
	})

	let max_amount = 0
	for (let i = 0;i < recipe_inputs.length;i++) {

		let inventory_amount = get_amount_in_inventory(recipe_inputs[i].itemid, inventory)
		let temp_crafting_amount = Math.floor(inventory_amount/recipe_inputs[i].amount)
		if (temp_crafting_amount < max_amount || max_amount == 0) {

			max_amount = temp_crafting_amount

		}

	}
	return max_amount

}

function have_enough(amount, recipe, inventory) {

	return max_crafting_amount(recipe, inventory) >= amount

}

async function post_data(url, data) {

	const response = await fetch(url, {
		method: 'POST',
		headers: {
		  'Content-Type': 'application/json'
		},
		body: JSON.stringify(data) // body data type must match "Content-Type" header
	});

}

class CraftingModal extends React.Component {

	constructor(props) {

		super(props)
		this.state = {

			display_craft_x: false,
			can_craft_1: have_enough(1, this.props.recipe, this.props.inventory),
			can_craft_5: have_enough(5, this.props.recipe, this.props.inventory),
			amount_value: 0

		}

	}

	craft_x = (amount) => {

		if (!have_enough(amount, this.props.recipe, this.props.inventory)) {
			return;
		}

		let converted_amount = Number(amount)

		if (isNaN(converted_amount)) {
			console.log("Is Nan")
			return
		}
		post_data("http://em_crafting/craft", {amount: converted_amount, recipe: this.props.recipe})

	}

	handle_amount_change = (event) => {

		this.setState({amount_value: event.target.value})

	}

	render() {

		let button_color_craft_1 = this.state.can_craft_1 ? 'button modal_buttons is-primary' : 'button modal_buttons is-light'
		let button_color_craft_5 = this.state.can_craft_5 ? 'button modal_buttons is-primary' : 'button modal_buttons is-light'

		return (
			<>
				<div className="modal is-active">
				  <div className="modal-background"></div>
				  <div className="modal-card">

				    <header className="modal-card-head">
				      <p className="modal-card-title">Making {this.props.recipe.recipe_name}</p>
				      <button className="delete" aria-label="close" onClick={this.props.no_modal}></button>
				    </header>

				    <div class="modal-card-body">

							{this.props.recipe.inputs.map((input) => {
								return <p><b>Required: </b> {input.amount}x {input.itemname} </p>
							})}

							<p>Able to craft {max_crafting_amount(this.props.recipe, this.props.inventory)}</p>
							{this.state.display_craft_x ? <> <input class="input" type="text" value={this.state.amount_value} onChange={this.handle_amount_change} placeholder="Craft X"/> </> : null}

							<button className={button_color_craft_1} onClick={() => this.craft_x(1)}>Craft 1</button>
							<button className={button_color_craft_5} onClick={() => this.craft_x(5)}>Craft 5</button>
							<button className={button_color_craft_1} onClick={() => !this.state.display_craft_x ? this.setState({display_craft_x: true}) : this.craft_x(this.state.amount_value)}>Craft X</button>
				    </div>
				  </div>
				</div>
			</>
		);

	}

}

class CraftingColumn extends React.Component {

	state = {

		display_modal: false

	}

	show_modal = () => {

		this.setState({display_modal: true})

	}

	no_modal = () => {

		this.setState({display_modal: false})

	}

	render() {

		let url = '/img/items/' + this.props.recipe.recipe_name.replace(" ", "_").toLowerCase() + '.png'

		return (
			<>
				<div 
					className="column is-1" 
					onClick={this.show_modal} 
					onMouseEnter={this.props.mouse_enter} 
					onMouseLeave={this.props.mouse_exit}
					ref={this.props.reference_element}>
					<center><img src={url} style={{width: '64px', height: '64px'}} alt={this.props.recipe.recipe_name}/></center>
				</div>
				{this.state.display_modal ? <CraftingModal no_modal={this.no_modal} recipe={this.props.recipe} inventory={this.props.inventory}/> : null}
			</>
		);

	}

}

function RecipeInfo(props) {

	const [visible, setVisibility] = useState(false);
	const [referenceElement, setReferenceElement] = useState(null);
	const [popperElement, setPopperElement] = useState(null);
	const [arrowElement, setArrowElement] = useState(null);
	const { styles, attributes } = usePopper(referenceElement, popperElement, {
		modifiers: 
		[
			{ 
				name: 'arrow', 
				options: { element: arrowElement } 
			}
		],
	});

  let popper_element = (
	<div ref={setPopperElement} style={styles.popper} {...attributes.popper} className="tooltip">

		<b>Item name:</b> {props.recipe.recipe_name}
		{props.recipe.inputs.map((input) => {

			return (
				<p><b>Required: </b> {input.amount}x {input.itemname} ({get_amount_in_inventory(input.itemid, props.inventory)}) </p>
			)

		})}

		<div ref={setArrowElement} style={styles.arrow} />

	</div>
  );

  return (
    <>
			<CraftingColumn recipe={props.recipe} inventory={props.inventory} reference_element={setReferenceElement} mouse_enter = {() => setVisibility(true)} mouse_exit = {() => setVisibility(false)}/>
			{visible ? popper_element : null}
    </>
  );

};

class CraftingCategory extends React.Component {

	state = {

		display_grid: false

	}

	render() {

		let elements = []
		for (let i = 0; i < this.props.recipes.length;i++) {

			elements.push(<RecipeInfo recipe={this.props.recipes[i]} inventory={this.props.inventory}/>)

		}

		let max_columns = 8
		let columns = []
		let num_columns = Math.ceil(elements.length/max_columns)
		for (let i = 0;i < num_columns;i++) {

			if (i * max_columns + max_columns > elements.length) {

				columns.push(<div className="columns">{elements.slice(i * max_columns, elements.length)}</div>)

			} else {

				columns.push(<div className="columns">{elements.slice(i * max_columns, i * max_columns + max_columns)}</div>)

			}

		}

		return columns

	}
	
}

class Crafting extends React.Component {

	render() {

		let items = []
		for (let key in crafting_categories) {

			if (this.props.recipes.some((element) => element.crafting_category_id == key)) {

				items.push(<h1 className="crafting-titles">{crafting_categories[key]}</h1>)
				items.push(<CraftingCategory recipes={ this.props.recipes.filter((element) => element.crafting_category_id == key) } inventory={this.props.inventory}/>)

			}

		}

		return items;

	}

}

export default Crafting;