
import React from 'react';
import './css/title.css'
import cog from './img/cog-and-wrench.png';

class Title extends React.Component {

	constructor(props) {

		super(props)

	}

	change_title(title) {

		this.setState({title: title})

	}

	render() {

		return (
			<div className="crafting-title-container">
				<img src={cog} className="crafting-cog" alt="cog" />
				<h1 className="crafting-title">
					{this.props.title}
				</h1>
			</div>
		)

	}

}

export default Title;