
import React from 'react';
import Title from './title';
import Crafting from './crafting_categories.js'
import './css/app.css'

class App extends React.Component {

	state = {
		display: false,
		inventory: [],
		recipes: [],
		title: "Placeholder"
	}

	constructor(props) {

		super(props)

	}

	event_listener_event(app_obj, event) {

		if (event.data.display != null) {

			app_obj.setState({display: event.data.display})

		}
		if (event.data.recipes != null) {

			app_obj.setState({recipes: event.data.recipes})
			app_obj.setState({inventory: event.data.inventory})
			app_obj.setState({title: event.data.title})

		}

	}

	key_up_handler(app_obj, key) {

		if (key.code == "Escape") {
		    
		    const http = new XMLHttpRequest()
			http.open("POST", "http://em_crafting/close")
			http.setRequestHeader('Content-type', 'application/json')
			http.send()

			http.onload = function() {

				app_obj.setState({display: false})

			}

			http.onerror = function() {}
			http.ontimeout = function() {}

		}

	}

	componentDidMount() {

		let event_listener = this.event_listener_event
		let keyup_handler = this.key_up_handler
		let app_obj = this
		window.addEventListener("message", function(event) {

			event_listener(app_obj, event)

		})

		document.addEventListener('keyup', function(key) {
			keyup_handler(app_obj, key)
		})

	}

	render() {

		let class_name = this.state.display ? 'crafting-app-container' : 'crafting-app-container-hidden'

		return (

			<div className={class_name}>
				<Title title={this.state.title}/>
				<Crafting recipes={this.state.recipes} inventory={this.state.inventory}/>
			</div>

		);

	}

}

export default App;